import * as S from './Header.style'
import { ReactChild, FC, useEffect } from 'react'
import Menu from '../../components/Menu/Menu'
import Menu__item from '../../components/Menu__item/Menu__item'
import LoginIcon from '@mui/icons-material/Login';
import { useMediaQuery, useTheme } from '@mui/material';
const { log } = console

interface HeaderProps {

	children?: ReactChild,

}

const Header: FC<HeaderProps> = ({ children }) => {

	const mobileVersion = useMediaQuery((theme: any) => theme.breakpoints.down('sm'))


	return (

		<S.Header component={'header'}>

			<Menu />
			{!mobileVersion &&
				<Menu__item startIcon={<LoginIcon />} sx={{ margin: '0' }} href='#'>
					
				</Menu__item>
			}
			

		</S.Header>
	)
}
export default Header