import * as S from './MenuBurger.style'
import { ReactChild, FC, useState } from 'react'
import { Box, Divider, List, ListItem, ListItemButton, ListItemText } from '@mui/material'
import MenuIcon from '@mui/icons-material/Menu';
import { Menu__item } from '../Menu__item/Menu__item.style';
const { log } = console

interface MenuBurgerProps {

	menuItems: string[]

}


const MenuBurger: FC<MenuBurgerProps> = ({ menuItems }) => {
	const [isMenuBurgerOpen, setIsMenuBurgerOpen] = useState(false)
	return (<>
		<S.BurgerButton onClick={() => setIsMenuBurgerOpen(!isMenuBurgerOpen)}>
			<MenuIcon color='primary' />
		</S.BurgerButton>

		<S.MenuBurger
			open={isMenuBurgerOpen}
			anchor={'right'}
			
		>
			<S.MenuBurger__Items
				
				onClick={() => setIsMenuBurgerOpen(false)}
				onKeyDown={() => setIsMenuBurgerOpen(false)}
			>
				
					{menuItems.map((text, index) => (
						<Menu__item key={index} href={`#${text}`} >
							{text}
						</Menu__item>
					))}
				
				<Divider />

			</S.MenuBurger__Items>
		</S.MenuBurger>

	</>)
}
export default MenuBurger